<template>
  <div class="navbar-wrapper">
    <c-scroll>
      <ul>
        <li :class="['navbar',{'active':value===index}]" v-for="(tab, index) in dataList" :key="index" @click="activeItem(index)">
          <div class="item">{{tab.title.slice(0,2)}}</div>
        </li>
      </ul>
    </c-scroll>
  </div>
</template>

<script>
// import { mapGetters, mapActions } from "vuex";

export default {
  name: "navBar",
  mounted() {
    this.$emit("mounted", this);
  },
  props: {
    dataList: {
      required: true
    },
    value: {
      type: Number
    }
  },
  computed: {},
  methods: {
    activeItem(index) {
      this.$emit("input", index);
    }
  },
  components: {
    "c-scroll": () =>
      import(/* webpackChunkName: "c-scroll" */ "@cube/scroll/scroll")
  }
};
</script>

<style lang="scss" scoped>
.navbar-wrapper {
  background-color: #f2f2f2;
  width: 4rem;
  padding: 0.5rem;
  font-size: 0.8rem;
  line-height: 1rem;
  overflow: hidden;
  text-align: center;

  .navbar {
    display: flex;
    flex-flow: column nowrap;
    height: 3rem;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
    &.active {
      //   outline: 1px solid rebeccapurple;
      font-size: 1rem;
      color: $main-theme-color;
    }
    .item {
      // text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      height: 2rem;
      margin: 1rem 0;
    }
  }
}
</style>
